var signals = {
  methods: {
    SIGNAL_CLEAR(sender, receiver = "terminal", slot = "slotClearTerm") {
      return {
        event: "clearTerm",
        sender: sender,
        receiver: receiver,
        slot: slot,
        kwargs: {}
      };
    },

    //  , 
    SIGNAL_DEPENDS_ON_MEMORY(sender, filename = null, receiver = "connector", slot = "slotSendCommands") {
      return {
        event: "dependsOnMemory",
        sender: sender,
        receiver: receiver,
        slot: slot,
        kwargs: {
          filename: filename,
          command: this.$emp.memoryAnalysing(filename)
        }
      };
    },

    // ,,
    SIGNAL_GET_CODE(sender, filename = null, receiver = "connector", slot = "slotSendCommands") {
      return {
        event: "getCode",
        sender: sender,
        receiver: receiver,
        slot: slot,
        kwargs: {
          filename: filename,
          command: this.$emp.getCode(filename)
        }
      };
    },

    // , ,
    SIGNAL_GET_FILE(sender, filename = null, receiver = "connector", slot = "slotGetFile") {
      return {
        event: "getFile",
        sender: sender,
        receiver: receiver,
        slot: slot,
        kwargs: {
          filename: filename
        }
      };
    },

    SIGNAL_SEND_COMMAND(sender, command, receiver = "connector", slot = "slotSendCommands") {
      return {
        event: "sendComands",
        sender: sender,
        receiver: receiver,
        slot: slot,
        kwargs: {
          command: command
        }
      };
    }
  }
};

export default signals;
